.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#main_section {
  font-size: 1em;
  width: 600px;
}

.tlo {
  background-image: radial-gradient(circle at right, #fcd475, white);
}

.wstep {
  background-image: url('header_logo.png');
  background-repeat: no-repeat;
}

.prawa {
  text-align: center;
}

.nazwa_gminy {
  font-size: 2em;
}

#main_table {
  table-layout: fixed;
  width: 100%;
}
#main_table td {
  padding: 20px;
  text-align: center;
}
.dzial{
  font-size: 1.6em;
  color: black
}
.sekcja_dzial {
  font-size: 3em;
  color: black
}

.zolty {
  background-color: #f9c00c;
}

.wartosc {
  font-size: 3.2em;
  color: #ffffff;
}

.wiÄ™cej {
  font-size: 0.8em;
  color: green;
}

.mniej {
  font-size: 0.8em;
  color: red;
}

#nazwa {
  text-align: center;
  font-size: 5em;
}

.chart {
  display: grid;
  grid-template-columns: 0.4fr 0.6fr;
  grid-gap: 20px;
}

.legend {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2px;
}

.pieContainer {
  padding: 30px;
  height: 600px;
  position: relative;
}

.pieBackground {
  position: absolute;
  width: 600px;
  height: 600px;
  border-radius: 100%;
  box-shadow: 0 0 32px rgba(0,0,0,0.5);
}

.pie {
  position: absolute;
  width: 600px;
  height: 600px;
  border-radius: 100%;
  clip: rect(0px, 300px, 600px, 0px);
}

.hold {
  position: absolute;
  width: 600px;
  height: 600px;
  border-radius: 100%;
  clip: rect(0px, 600px, 600px, 300px);
}

.projekty, tr {
  border-collapse: collapse;
}

.projekty td {
  border: 1px solid black;
}

.autor {
  text-align: right;
  font-weight: bold;
}

.formControl {
  margin: 5px;
  min-width: 120px;
  max-width: 300px;
}

.chips {
  display: flex;
  flex-wrap: wrap;
}

.chip {
  margin: 2px;
}

.noLabel {
  margin-top: 10px;
}